.sendForm {
    display: flex;
    justify-content: space-between;
    margin: 20px
}

.textarea {
    padding: 10px;
    width: 80%;
    background-color: rgb(204, 204, 204);
    border: 1px solid  rgb(204, 204, 204);
    border-radius: 10px;
}

.button {
    margin-right: 10px;
    background-color: blue;
    border: 1px solid blue;
    border-radius: 10px;
    color: rgb(248, 248, 248);
    padding: 5px 10px;
}

.button:hover {
    background-color: rgb(255, 255, 255);
    border: 1px solid blue;
    border-radius: 10px;
    color: blue;
}
