.message {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: end;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 40px;
    height: 40px;
}

.text {
    margin-right: 10px;
    background-color: blue;
    border-radius: 10px;
    position: relative;
}

.text:after {
content: '';
  position: absolute;
  bottom: 0px;
  right: -10px;
  border-bottom: 10px solid blue;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  /* border-bottom: 10px solid transparent; */
}

.name {
    color:white;
}

.messageText {
    color:white;
}

