.friendMessage {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: start;
}

.friendImageAndText {
    display: flex;
    flex-direction: reverse;
}

.friendImageAndText img {
    width: 40px;
    height: 40px;
}

.friendText {
    margin-left: 10px;
    background-color: rgb(107, 107, 113);
    border-radius: 10px;
    position: relative;
}

.friendText:after {
    content: '';
  position: absolute;
  bottom: 0px;
  left: -10px;
  border-bottom: 10px solid rgb(107, 107, 113);
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;

}

